import React, { FunctionComponent } from 'react';
import getComponent from '../../../../utils/cms/component-resolver';
import ClassNames from './BlankPage.module.scss';
import AppLink from '../../../../util-components/Link/Link';
import { CmsPageContext } from '../../../../types/CmsPageContext';
import PageRoot from '../PageRoot/PageRoot';

interface Props {
    pageContext: {
        fields: {
            items: any[];
        };
    };
}

const BlankPage: FunctionComponent<Props & CmsPageContext> = props => {
    const fields = props.pageContext.fields;
    const components = fields.items.map(item => {
        const Comp = getComponent(item);
        return Comp;
    });

    return (
        <PageRoot {...props.pageContext.meta}>
            <div className={ClassNames.BlankPage}>
                <div>
                    <AppLink to="/">Home (hard for now)</AppLink>
                </div>
                {components}
            </div>
        </PageRoot>
    );
};

export default BlankPage;
